<template>
  <div class="home">
    <h1>ÏòÅÌôî Ï†ïÎ≥¥ ÏñªÎäî ÏÇ¨Ïù¥Ìä∏</h1>

    <!-- Loading -->
    <div v-if="loading" class="loading">Loading movies...</div>

    <!-- Sections -->
    <section v-else>
      <div
        class="movie-section"
        v-for="section in movieSections"
        :key="section.title"
      >
        <h2>{{ section.title }}</h2>
        <swiper
          :slides-per-view="5"
          space-between="20"
          navigation
          pagination
          class="movie-slider"
        >
          <swiper-slide
            v-for="movie in section.movies"
            :key="movie.id"
            :class="getCardClass(movie)"
            @click="toggleRecommendation(movie)"
          >
            <img
              :src="getPosterUrl(movie.poster_path)"
              :alt="movie.title"
              class="movie-poster"
            />
            <h3>{{ movie.title }}</h3>
            <p>{{ truncateDescription(movie.overview) }}</p>
            <p>‚≠ê Rating: {{ movie.vote_average }}</p>
            <p>üìÖ Release: {{ movie.release_date }}</p>
          </swiper-slide>
        </swiper>
      </div>
    </section>
  </div>
</template>

<script>
import axios from "axios";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { Swiper, SwiperSlide } from "swiper/vue";

export default {
  name: "HomePage",
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      movieSections: [
        { title: "Ïù∏Í∏∞ Í∏âÏÉÅÏäπ ÏòÅÌôî", movies: [] },
        { title: "ÏµúÍ≥† ÌèâÏ†ê ÏòÅÌôî", movies: [] },
        { title: "Í∞úÎ¥â ÏòàÏ†ï ÏòÅÌôî", movies: [] },
        { title: "Ïù∏Í∏∞ ÏòÅÌôî", movies: [] },
      ],
      recommendedMovies: [], // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú Î∂àÎü¨Ïò® Ï∂îÏ≤ú ÏòÅÌôî
      loading: true,
    };
  },
  methods: {
    async fetchMovies(url, sectionIndex, pages = 3) {
      try {
        const allMovies = [];
        for (let page = 1; page <= pages; page++) {
          const response = await axios.get(url, {
            params: { api_key: "338afe18473748636f29d4cb0fedaa87", page },
          });
          allMovies.push(...response.data.results);
        }

        // Ï§ëÎ≥µ Ï†úÍ±∞
        const uniqueMovies = this.removeDuplicates(allMovies);
        this.movieSections[sectionIndex].movies = uniqueMovies;
      } catch (error) {
        console.error(
          `Failed to fetch movies for ${this.movieSections[sectionIndex].title}`,
          error
        );
      }
    },
    removeDuplicates(movies) {
      const unique = [];
      const seen = new Set();

      movies.forEach((movie) => {
        if (!seen.has(movie.id)) {
          unique.push(movie);
          seen.add(movie.id);
        }
      });

      return unique;
    },
    getPosterUrl(path) {
      return `https://image.tmdb.org/t/p/w500${path}`;
    },
    truncateDescription(description) {
      return description.length > 100
        ? description.substring(0, 100) + "..."
        : description;
    },
    toggleRecommendation(movie) {
      const exists = this.recommendedMovies.some(
        (recommended) => recommended.id === movie.id
      );

      if (exists) {
        this.recommendedMovies = this.recommendedMovies.filter(
          (recommended) => recommended.id !== movie.id
        );
        alert(`${movie.title} has been removed from your Wishlist.`);
      } else {
        this.recommendedMovies.push(movie);
        alert(`${movie.title} has been added to your Wishlist.`);
      }

      this.saveToLocalStorage();
    },
    loadFromLocalStorage() {
      const saved = localStorage.getItem("recommendedMovies");
      this.recommendedMovies = saved ? JSON.parse(saved) : [];
    },
    saveToLocalStorage() {
      localStorage.setItem(
        "recommendedMovies",
        JSON.stringify(this.recommendedMovies)
      );
    },
    getCardClass(movie) {
      const isRecommended = this.recommendedMovies.some(
        (recommended) => recommended.id === movie.id
      );
      return isRecommended ? "movie-card recommended-card" : "movie-card";
    },
  },
  async created() {
    const urls = [
      "https://api.themoviedb.org/3/trending/movie/day",
      "https://api.themoviedb.org/3/movie/top_rated",
      "https://api.themoviedb.org/3/movie/upcoming",
      "https://api.themoviedb.org/3/movie/popular",
    ];

    const fetchPromises = urls.map(
      (url, index) => this.fetchMovies(url, index, 3) // ÌéòÏù¥ÏßÄ Ïàò ÏÑ§Ï†ï
    );
    await Promise.all(fetchPromises);

    this.loadFromLocalStorage();
    this.loading = false;
  },
};
</script>

