<template>
  <div id="app">
    <!-- ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î∞î -->
    <header v-if="!isSignInPage">
      <nav>
        <div class="nav-links">
          <router-link to="/" class="nav-link">Home</router-link>
          <router-link to="/popular" class="nav-link">Popular</router-link>
          <router-link to="/search" class="nav-link">Search</router-link>
          <router-link to="/wishlist" class="nav-link">Wishlist</router-link>
        </div>
        <!-- ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ ÌëúÏãú -->
        <div class="user-info" v-if="user">
          <span class="user-nickname">{{
            user.kakao_account.profile.nickname
          }}</span>
          <button class="icon-button" @click="logout">Logout</button>
        </div>
        <button class="icon-button" v-else @click="goToSignIn">
          <i class="icon-user"></i>
        </button>
      </nav>
    </header>
    <!-- ÎùºÏö∞ÌÑ∞ Î∑∞ -->
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      user: null, // Ïπ¥Ïπ¥Ïò§ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥
    };
  },
  computed: {
    // ÌòÑÏû¨ Í≤ΩÎ°úÍ∞Ä /signinÏù¥Í±∞ÎÇò ÌïòÏúÑ Í≤ΩÎ°úÏùº Í≤ΩÏö∞ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î∞î Ïà®ÍπÄ
    isSignInPage() {
      return this.$route.path.startsWith("/signin");
    },
  },
  methods: {
    goToSignIn() {
      this.$router.push("/signin"); // SignIn ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
    },
    logout() {
      // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î∞è ÌÜ†ÌÅ∞ Ï†úÍ±∞
      localStorage.removeItem("kakao_user");
      localStorage.removeItem("kakao_access_token");
      this.user = null; // ÏÇ¨Ïö©Ïûê ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî
      this.$router.push("/signin"); // Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏
      alert("Logged out successfully.");
    },
  },
  mounted() {
    // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î°úÎìú
    const savedUser = localStorage.getItem("kakao_user");
    if (savedUser) {
      this.user = JSON.parse(savedUser);
    }
  },
};
</script>

