<template>
  <div class="wishlist">
    <h1>Ï¶êÍ≤®Ï∞æÍ∏∞</h1>

    <!-- Table View -->
    <table v-if="recommendedMovies.length" class="wishlist-table">
      <thead>
        <tr>
          <th>#</th>
          <th>Ìè¨Ïä§ÌÑ∞</th>
          <th>Ï†úÎ™©</th>
          <th>ÌèâÏ†ê</th>
          <th>Í∞úÎ¥âÏùº</th>
          <th>ÏÉÅÌÉú</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(movie, index) in recommendedMovies"
          :key="movie.id"
          class="wishlist-row"
        >
          <td>{{ index + 1 }}</td>
          <td>
            <div class="poster-container">
              <img
                :src="getPosterUrl(movie.poster_path)"
                :alt="movie.title"
                class="movie-poster"
              />
            </div>
          </td>
          <td class="movie-title">{{ movie.title }}</td>
          <td class="movie-rating">‚≠ê {{ movie.vote_average }}</td>
          <td class="movie-release">{{ movie.release_date }}</td>
          <td>
            <button @click="removeFromWishlist(movie)" class="remove-button">
              Ï†úÍ±∞
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- No Movies -->
    <div v-else class="empty-message">Ï¶êÍ≤®Ï∞æÍ∏∞ Ìïú ÏòÅÌôîÍ∞Ä ÏóÜÏäµÎãàÎã§.</div>
  </div>
</template>

<script>
export default {
  name: "MovieWishlist",
  data() {
    return {
      recommendedMovies: [], // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú Î∂àÎü¨Ïò® Îç∞Ïù¥ÌÑ∞
    };
  },
  methods: {
    getPosterUrl(path) {
      return `https://image.tmdb.org/t/p/w500${path}`;
    },
    removeFromWishlist(movie) {
      this.recommendedMovies = this.recommendedMovies.filter(
        (m) => m.id !== movie.id
      );
      this.saveToLocalStorage();
    },
    loadFromLocalStorage() {
      const saved = localStorage.getItem("recommendedMovies");
      if (saved) {
        this.recommendedMovies = JSON.parse(saved);
      }
    },
    saveToLocalStorage() {
      localStorage.setItem(
        "recommendedMovies",
        JSON.stringify(this.recommendedMovies)
      );
    },
  },
  created() {
    this.loadFromLocalStorage();
  },
};
</script>

