<template>
  <div class="popular">
    <h1>Ïù∏Í∏∞ ÏòÅÌôî</h1>
    <!-- Table View -->
    <div class="table-view">
      <table>
        <thead>
          <tr>
            <th>Ìè¨Ïä§ÌÑ∞</th>
            <th>Ï†úÎ™©</th>
            <th>Í∞úÎ¥âÏùº</th>
            <th>ÌèâÏ†ê</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="movie in currentPageMovies"
            :key="movie.id"
            @click="toggleWishlist(movie)"
            :class="{ added: isInWishlist(movie) }"
          >
            <td>
              <div class="poster-container">
                <img
                  :src="getPosterUrl(movie.poster_path)"
                  :alt="movie.title"
                />
              </div>
            </td>
            <td>{{ movie.title }}</td>
            <td>{{ movie.release_date }}</td>
            <td>{{ movie.vote_average }}</td>
          </tr>
        </tbody>
      </table>
      <!-- Pagination -->
      <div class="pagination">
        <button :disabled="currentPage === 1" @click="prevPage">
          Previous
        </button>
        <span>Page {{ currentPage }}</span>
        <button :disabled="currentPage === totalPages" @click="nextPage">
          Next
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "PopularMovies",
  data() {
    return {
      movies: [], // ÏòÅÌôî Îç∞Ïù¥ÌÑ∞ Ï†ÄÏû•
      currentPage: 1, // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
      totalPages: 0, // Ï¥ù ÌéòÏù¥ÏßÄ Ïàò
      moviesPerPage: 10, // ÌéòÏù¥ÏßÄÎãπ ÏòÅÌôî Ïàò
      recommendedMovies: [], // Ï∂îÏ≤úÎêú ÏòÅÌôî (Local Storage)
    };
  },
  computed: {
    currentPageMovies() {
      const start = (this.currentPage - 1) * this.moviesPerPage;
      const end = start + this.moviesPerPage;
      return this.movies.slice(start, end);
    },
  },
  methods: {
    async fetchMovies(pages = 5) {
      const allMovies = [];
      for (let page = 1; page <= pages; page++) {
        const response = await axios.get(
          "https://api.themoviedb.org/3/movie/popular",
          {
            params: { api_key: "338afe18473748636f29d4cb0fedaa87", page },
          }
        );
        allMovies.push(...response.data.results);
      }

      // Ï§ëÎ≥µ Ï†úÍ±∞ ÌõÑ ÏòÅÌôî Îç∞Ïù¥ÌÑ∞ Ï†ÄÏû•
      this.movies = this.removeDuplicates(allMovies);

      // Ï¥ù ÌéòÏù¥ÏßÄ Ïàò Í≥ÑÏÇ∞
      this.totalPages = Math.ceil(this.movies.length / this.moviesPerPage);
    },
    getPosterUrl(path) {
      return `https://image.tmdb.org/t/p/w500${path}`;
    },
    isInWishlist(movie) {
      return this.recommendedMovies.some((item) => item.id === movie.id);
    },
    toggleWishlist(movie) {
      const exists = this.isInWishlist(movie);
      if (exists) {
        this.recommendedMovies = this.recommendedMovies.filter(
          (item) => item.id !== movie.id
        );
      } else {
        this.recommendedMovies.push(movie);
      }
      this.saveToLocalStorage();
    },
    loadFromLocalStorage() {
      const saved = localStorage.getItem("recommendedMovies");
      this.recommendedMovies = saved ? JSON.parse(saved) : [];
    },
    saveToLocalStorage() {
      localStorage.setItem(
        "recommendedMovies",
        JSON.stringify(this.recommendedMovies)
      );
    },
    removeDuplicates(movies) {
      const unique = [];
      const seen = new Set();

      movies.forEach((movie) => {
        if (!seen.has(movie.id)) {
          unique.push(movie);
          seen.add(movie.id);
        }
      });

      return unique;
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
  },
  async created() {
    await this.fetchMovies(5); // 5Í∞úÏùò ÌéòÏù¥ÏßÄ Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞
    this.loadFromLocalStorage();
  },
};
</script>

